@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
div.head-container {
  position: relative;
  width: 173px;
  height: 168px;
}

div.head-container img {
  position: absolute;
}

div.head-container img.Head-Hair {
  z-index: 1;
  top: -35px;
  left: 10px;
}

div.head-container img.Head-Eyebrows {
  z-index: 1;
  top: 40px;
  left: 30px;
}

div.head-container img.Head-Eyebrows.Head-Eyebrows-Right {
  left: 100px;
}

div.head-container img.Head-Eyes {
  z-index: 1;
  top: 60px;
  left: 40px;
}

div.head-container img.Head-Eyes.Head-Eyes-Right {
  left: 110px;
}

div.head-container img.Head-Nose {
  z-index: 1;
  top: 80px;
  left: 70px;
}

div.head-container img.Head-Mouth {
  z-index: 1;
  top: 120px;
  left: 73px;
}

div.head-container button {
  z-index: 2;
  position: absolute;
  bottom: -50px;
}

.Right {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

div.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: black;
  font-family: "Courier New", Courier, monospace;
  /* font-family: "Press Start 2P", "Courier New", Courier, monospace; */
}

html,
body,
div#root,
div.App {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

ul li {
  list-style: none;
}

button,
input,
textarea,
a {
  outline: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

button,
a,
label,
select,
option {
  cursor: pointer;
}

a {
  text-decoration: none;
}

